@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap);
html{scroll-behavior:smooth}*,*::after *::before{box-sizing:border-box;padding:0;margin:0}body{font-family:'Montserrat', sans-serif;padding:0;margin:0}

.navigation{z-index:3;position:fixed;top:0;left:0;display:flex;align-items:center;justify-content:space-between;width:100%;height:85px;background-color:#16213eee;padding:0 46px;box-sizing:border-box}@media (max-width: 786px){.navigation{padding:16px}}.navMenu{display:flex;list-style:none}@media (max-width: 960px){.navMenu{transition:0.3s ease-in-out 0.1s left;position:fixed;margin:0;padding:0;top:0;width:100%;height:100%;background-color:#16213E;display:flex;flex-direction:column;justify-content:center;align-items:center;list-style:none;left:-1000px}}.navMenu-mobile{transition:0.3s ease-in-out 0.1s left;position:fixed;margin:0;padding:0;top:0;width:100%;height:100%;background-color:#16213E;display:flex;flex-direction:column;justify-content:center;align-items:center;list-style:none;left:0}.navItem{padding:0 40px 0 0;transition:.3s transform ease-in-out}.navItem:hover{transform:scale(1.05)}@media (max-width: 960px){.navItem{margin-bottom:50px;padding:0}}.navLink{text-decoration:none;font-size:36px;color:#ffffff}.active{font-weight:500}.logo{font-size:36px;font-weight:400;color:#ffffff}.logo__first{font-weight:700}@media (max-width: 786px){.logo{transform:scale(0.7)}}

.hamburger{padding:10px;display:inline-block;cursor:pointer;background-color:transparent;bottom:0;margin-right:0;border:none;z-index:2}@media (min-width: 960px){.hamburger{display:none}}.hamburger__box{width:40px;height:24px;display:inline-block;position:relative}.hamburger__inner{width:100%;height:3px;background-color:#1C5CA8;position:absolute;left:0;top:50%;transition:background-color 0.3s ease-in-out 0.1s}.hamburger__inner::after,.hamburger__inner::before{content:'';width:100%;height:3px;background-color:#1C5CA8;position:absolute;left:0;transition:transform 0.3s ease-in-out 0.1s}.hamburger__inner::after{top:10px}.hamburger__inner::before{top:-10px}.hamburger__active .hamburger__inner{background-color:transparent}.hamburger__active .hamburger__inner::after{transform:translateY(-10px) rotate(-45deg)}.hamburger__active .hamburger__inner::before{transform:translateY(10px) rotate(45deg)}

.hero{width:100%;background-color:#16213E}.hero__box{max-width:1440px;margin:0 auto;padding:0 16px 0 16px;display:flex;justify-content:space-between;align-items:center;height:80vh;position:relative}@media (max-width: 960px){.hero__box{justify-content:center}}@media (max-width: 786px){.hero__content{display:flex;flex-direction:column;justify-content:center;align-items:center}}.hero__img{position:absolute;bottom:-200px;left:0}@media (min-width: 1600px){.hero__img{transform:scale(1.2)}}@media (max-width: 1150px){.hero__img{transform:scale(0.7);bottom:-270px;left:-20px}}@media (max-width: 960px){.hero__img{display:none}}.hero__header{color:#fff;margin:0;font-size:64px;font-weight:normal;animation-name:animation;animation-duration:15s;animation-iteration-count:infinite}@media (max-width: 549px){.hero__header{font-size:42px}}.hero__header--second{color:#fff;margin:0;font-size:48px;font-weight:normal;padding-bottom:70px}@media (max-width: 549px){.hero__header--second{font-size:24px}}.hero__icon--img{position:absolute;bottom:-60px;right:0}@media (max-width: 960px){.hero__icon--img{transform:scale(0.8);position:absolute;bottom:-100px;right:-180px}}@media (max-width: 786px){.hero__icon--img{display:none}}.hero__button{background-color:#36A9E1;color:#16213E;font-weight:bolder;text-decoration:none;font-size:24px;padding:15px 60px;border-radius:10px;transition:.3s background-color ease-in-out}.hero__button:hover{background-color:#6bd2df}@media (max-width: 960px){.hero__button{font-size:16px}}@keyframes animation{0%{color:#fff}50%{color:#36A9E1}100%{color:#fff}}

.pricing{background-color:#F4F7FA;width:100%;padding-bottom:64px}.pricing__box{max-width:1440px;margin:0 auto}.pricing__header{margin:0;text-align:center;font-size:48px;color:#16213E;padding:200px 0 100px 0}@media (max-width: 786px){.pricing__header{padding:100px 0 40px 0;font-size:36px}}.pricing__flex{display:flex;flex-direction:row;justify-content:center;align-items:center}@media (max-width: 960px){.pricing__flex{flex-direction:column}}.package{background-color:#36A9E1;max-width:350px;display:flex;flex-direction:column;justify-content:center;align-items:center;margin:16px;transition:.3s transform ease-in-out}.package:hover{transform:scale(1.01)}.package__header{font-size:36px;color:#fff;font-weight:600;margin:22px 0}@media (max-width: 786px){.package__header{font-size:24px}}.package__price{font-size:48px;color:#fff;font-weight:600;margin:12px 0 48px 0}@media (max-width: 786px){.package__price{font-size:36px}}.package__description{font-size:24px;color:#fff;font-weight:400}@media (max-width: 786px){.package__description{font-size:16px}}.package__button{color:#fff;background-color:#0F3460;padding:24px 64px;border-radius:16px;margin:64px 18px 42px 18px;text-decoration:none;font-size:24px}@media (max-width: 786px){.package__button{font-size:16px}}.package__img{padding:32px 0 16px}.decorated{background-color:#0F3460;transform:scale(1.02);transition:.3s transform ease-in-out}.decorated:hover{transform:scale(1.03)}.decorated__button{color:#fff;background-color:#36A9E1;padding:24px 64px;border-radius:16px;margin:64px 18px 42px 18px;text-decoration:none;font-size:24px}@media (max-width: 786px){.decorated__button{font-size:16px}}

.informations{background-color:#F4F7FA}.informations__container{padding:150px;max-width:1440px;margin:0 auto;position:relative;background-color:#1C5CA8;box-shadow:0px 4px 15px 10px rgba(0,0,0,0.25)}@media (max-width: 1250px){.informations__container{padding:150px 30px}}@media (max-width: 960px){.informations__container{padding:16px;display:flex;flex-direction:column;justify-content:center;align-items:center}}.informations__header{font-size:48px;color:#ffffff;font-weight:600}@media (max-width: 786px){.informations__header{text-align:center;font-size:24px}}.informations__text{z-index:2;color:#fff;font-size:24px;max-width:500px;font-weight:400}@media (max-width: 786px){.informations__text{font-size:18px}}.informations__img{position:absolute;display:inline;right:0;bottom:-15px;z-index:1}@media (max-width: 1150px){.informations__img{transform:scale(0.7);transform-origin:bottom right}}@media (max-width: 960px){.informations__img{transform:scale(0.5);opacity:0.1;transform-origin:bottom right}}

.footer{background-color:#F4F7FA}.footer__container{background-color:#0F3460;padding:16px 150px;max-width:1440px;margin:0 auto;position:relative}@media (max-width: 786px){.footer__container{padding:0}}.footer__sections{padding-top:150px;display:flex;justify-content:center}@media (max-width: 786px){.footer__sections{flex-direction:column;align-items:center;padding:0;text-align:center}}.footer__section{display:flex;flex-direction:column;padding:0 64px;color:#fff}.footer__section--header{font-size:32px}@media (max-width: 786px){.footer__section--header{border-bottom:1px solid #fff}}.footer__section--Link{display:inline-block;color:#fff;font-size:24px;text-decoration:none;line-height:42px;transition:.3s transform ease-in-out;transition:.3s transform ease-in-out}.footer__section--Link:hover{transform:scale(1.05)}.footer__icons{display:flex;justify-content:center;align-items:center}.footer__icon{padding:55px;transition:.3s transform ease-in-out}.footer__icon:hover{transform:scale(1.1)}@media (max-width: 786px){.footer__icon{transform:scale(0.8);padding:16px 16px 64px 16px}}.footer__logo{font-size:36px;font-weight:400;color:#ffffff;position:absolute;left:50px;bottom:10px}.footer__logo--first{font-weight:700}

